'use strict';
var _self;
import { TESTSUCCESS, TESTFAIL} from '@/config/image.js';
import { DECORATION} from '@/config/router.js';
import { getCount } from '@/api/todoChild/loan.js';
export default {
	data(){
		return{
			imgsuccess:TESTSUCCESS,
			imgfail:TESTFAIL,
			show:false
		}
	},
	methods:{
		submit(){
			uni.switchTab({
				url:DECORATION
			})
		}
	},
	async onLoad() {
		_self =this
		let e= await getCount()
		console.log(e)
		if (e.count == '0'){
			_self.show=true
		}
	}
};
